import React, { Component } from 'react';
import { Link } from 'react-router-dom';

const NavBar = () => (

    <nav>
        <ul>
            <li>
                <Link to={'/'}>Home</Link>
            </li>
            <li>
                <Link to={'/about'}>About</Link>
            </li>
            <li>
                <Link to={'/articles-list'}>Articles List</Link>
            </li>
        </ul>
    </nav>

// Use the Link component instead of standard 'a' tags for hyperlinks which navigate to other pages. 
// It is a part of react-router-dom and must be imported to do so. 

// Not a bad option when it comes to doing a navbar in React. 

);

export default NavBar;