import React, { Component } from 'react';

const AboutPage = () => (
    <>
    <h1>About</h1>
    <p>
    Apollo, which is YRC’s biggest creditor, 
    said it was not involved in YRC’s decision to seek the Treasury Department loan and noted that it is one of several lenders invested in the company. 
    However, a spokeswoman acknowledged that it was advised of the government loan and made changes to its own loan terms with YRC to accommodate it.
    </p>
    <p>
    The relationship between Apollo and the White House runs deep. 
    In 2017, Josh Harris, a founder of Apollo, advised the Trump administration on 
    infrastructure policy and discussed a possible White House job with Jared Kushner, Trump’s son-in-law and senior adviser. 
    That same year, Apollo lent $184 million to Kushner’s family real estate firm, Kushner Cos., to refinance the mortgage on a Chicago skyscraper.
    </p>
    </>
)

export default AboutPage;